@import "partials/reset.css";
@import "partials/fonts.css";
/* my style */
input,
button,
textarea {
  font-size: inherit;
  font-family: inherit;
  margin: 0;
  padding: 0;
  border: 0;
}

div,
input,
textarea,
select,
button,
h1,
h2,
h3,
h4,
h5,
h6,
a,
span {
  outline: none;
  font-size: inherit;
  background: transparent;
}

* {
  box-sizing: border-box;
}

a,
a:hover {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

textarea {
  resize: none;
}

a {
  color: inherit;
}

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  color: #ffffff;
  background-color: #1a1919;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
}
body.scroll-stoped {
  overflow: hidden;
}

.main {
  flex-grow: 1;
  background-color: #f9f9f9;
}

html,
body {
  height: 100%;
}

.container {
  box-sizing: border-box;
  width: 1137px;
  max-width: 95%;
  max-width: calc(100% - 60px);
  margin-left: 6.73611vw;
  margin-right: 14.305556vw;
}

@media screen and (min-width: 1500px) {
  .container {
    margin: 0 auto;
  }
}
@media screen and (max-width: 1350px) {
  .container {
    margin: 0 auto;
  }
}
@media screen and (max-width: 550px) {
  .container {
    max-width: calc(100% - 30px);
  }
}
#jGrowl {
  z-index: 999999;
}

*:focus {
  outline: none;
}

.hide {
  display: none;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}
body.compensate-for-scrollbar {
  overflow: hidden;
}
body::-webkit-scrollbar {
  width: 7px;
}
body::-webkit-scrollbar-track {
  background-color: #1A1919;
}
body::-webkit-scrollbar-thumb {
  border-radius: 8px;
  border: 1px solid #1A1919;
  background-color: #9b8a60;
}

#page {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
}

html {
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

.logo {
  display: inline-block;
  flex-shrink: 0;
}
.logo img {
  width: 109px;
  height: 24px;
}

.inner {
  display: flex;
}
.inner-left {
  flex-basis: 23.48285%;
  display: flex;
}
.inner-right {
  flex-basis: 76.51715%;
  display: flex;
}

.title {
  font-family: "Calibri", sans-serif;
  font-weight: bold;
  color: #ffffff;
  position: relative;
  z-index: 1;
}
.title--big {
  font-size: 80px;
  line-height: 77px;
}
@media screen and (max-width: 1200px) {
  .title--big {
    font-size: 64px;
    line-height: 64px;
  }
}
@media screen and (max-width: 993px) {
  .title--big {
    font-size: 52px;
    line-height: 52px;
  }
}
@media screen and (max-width: 767px) {
  .title--big {
    font-size: 45px;
    line-height: 45px;
  }
}
@media screen and (max-width: 400px) {
  .title--big {
    font-size: 40px;
    line-height: 100%;
  }
}
@media screen and (max-width: 370px) {
  .title--big {
    font-size: 35px;
    line-height: 100%;
  }
}
.title--small {
  font-size: 27px;
  line-height: 77px;
}

.section-title {
  font-weight: bold;
  font-size: 27px;
  line-height: 43px;
}

.start-header {
  padding-top: 32px;
  padding-left: 64px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
}
@media only screen and (max-width: 993px) {
  .start-header {
    padding-left: 30px;
  }
}
.start-body {
  color: #ffffff;
  display: flex;
  height: 100vh;
  background-color: #1a1919;
}
@media only screen and (max-width: 1025px) {
  .start-body {
    flex-direction: column;
  }
}
.start-body__title {
  position: relative;
  z-index: 10;
  font-family: "Calibri", sans-serif;
  font-weight: bold;
  font-size: 64px;
  line-height: 55px;
  max-width: 532px;
}
@media only screen and (max-width: 1200px) {
  .start-body__title {
    font-size: 5vw;
  }
}
@media only screen and (max-width: 550px) {
  .start-body__title {
    font-size: 36px;
    max-width: 350px;
  }
}
@media only screen and (max-width: 400px) {
  .start-body__title {
    font-size: 26px;
    line-height: 150%;
    max-width: 300px;
  }
}
.start-body__inner {
  position: relative;
  flex-basis: 50%;
  padding-top: 211px;
  padding-bottom: 132px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding-left: 64px;
  height: 100%;
}
@media only screen and (max-width: 1025px) {
  .start-body__inner {
    padding-top: 150px;
    padding-bottom: 100px;
    padding-left: 30px;
  }
}
@media only screen and (max-width: 550px) {
  .start-body__inner {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
@media only screen and (max-width: 993px) {
  .start-body__left {
    height: 50vh;
  }
}
@media only screen and (max-width: 550px) {
  .start-body__left {
    padding-top: 100px;
  }
}
@media only screen and (max-width: 350px) {
  .start-body__left {
    padding-top: 80px;
  }
}
.start-body__left-bg {
  width: 26.5vw;
  height: 50.39vw;
  bottom: 0;
  right: 6vw;
  z-index: 0;
}
@media only screen and (max-width: 1200px) {
  .start-body__left-bg {
    right: 0;
  }
}
@media screen and (min-width: 1500px) {
  .start-body__left-bg {
    width: 494px;
    height: 724px;
  }
}
@media only screen and (max-width: 993px) {
  .start-body__right {
    height: 50vh;
  }
}
@media only screen and (max-width: 1025px) {
  .start-body__right {
    padding-top: 100px;
  }
}
@media only screen and (max-width: 550px) {
  .start-body__right {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
.start-body__right-bg {
  width: 25.62vw;
  height: 39.556vw;
  bottom: 0;
  right: 2vw;
  z-index: 0;
}
@media only screen and (max-width: 1200px) {
  .start-body__right-bg {
    right: 0;
  }
}
@media screen and (min-width: 1500px) {
  .start-body__right-bg {
    width: 369px;
    height: 570px;
  }
}
.start-body__button {
  position: relative;
  z-index: 10;
  color: #ffffff;
  font-size: 16px;
  line-height: 39px;
  padding: 10px 22px;
}
@media only screen and (max-width: 1025px) {
  .start-body__button {
    margin-top: 50px;
  }
}
@media only screen and (max-width: 350px) {
  .start-body__button {
    margin-top: 20px;
    padding: 8px 15px;
    font-size: 14px;
  }
}
.start-body__bg {
  position: absolute;
}
@media only screen and (max-width: 1025px) {
  .start-body__bg {
    height: 350px;
    width: auto;
  }
}
@media only screen and (max-width: 550px) {
  .start-body__bg {
    height: 200px;
    right: 5%;
  }
}
@media only screen and (max-width: 400px) {
  .start-body__bg {
    right: 0;
  }
}
.start-body__bg img {
  object-fit: cover;
  height: 100%;
}